import '@google/model-viewer';
import { useState } from 'react';

import './ModelViewer.scss';

const Model = () => {
    const [minXAngle, setMinXAngle] = useState('');
    const [maxXAngle, setMaxXAngle] = useState('');
    const [minYAngle, setMinYAngle] = useState('');
    const [maxYAngle, setMaxYAngle] = useState('');
    const [minZoomDis, setMinZoomDis] = useState('');
    const [maxZoomDis, setMaxZoomDis] = useState('');

    return (
        <div className="viewer">
            <div className="viewer__navBar">
                <div className="viewer__navBar__item">
                    <div className="desc"> min X Axis Angle : </div>
                    <input type="number" className="form-control" placeholder="min X Axis Angle" onChange={(e) => setMinXAngle(e.target.value)}></input>
                </div>
                <div className="viewer__navBar__item">
                    <div className="desc"> max X Axis Angle : </div>
                    <input type="number" className="form-control" placeholder="max X Axis Angle" onChange={(e) => setMaxXAngle(e.target.value)}></input>
                </div>
                <div className="viewer__navBar__item">
                    <div className="desc"> min Y Axis Angle : </div>
                    <input type="number" className="form-control" placeholder="min Y Axis Angle" onChange={(e) => setMinYAngle(e.target.value)}></input>
                </div>
                <div className="viewer__navBar__item">
                    <div className="desc"> max Y Axis Angle : </div>
                    <input type="number" className="form-control" placeholder="max Y Axis Angle" onChange={(e) => setMaxYAngle(e.target.value)}></input>
                </div>
                <div className="viewer__navBar__item">
                    <div className="desc"> min Zoom Dis : </div>
                    <input type="number" className="form-control" placeholder="min Zoom Dis" onChange={(e) => setMinZoomDis(e.target.value)}></input>
                </div>
                <div className="viewer__navBar__item">
                    <div className="desc"> max Zoom Dis : </div>
                    <input type="number" className="form-control" placeholder="max Zoom Dis" onChange={(e) => setMaxZoomDis(e.target.value)}></input>
                </div>
            </div>
    
            <model-viewer
                src="/Astronaut.glb"
                ios-src=""
                poster="https://cdn.glitch.com/36cb8393-65c6-408d-a538-055ada20431b%2Fposter-astronaut.png?v=1599079951717"
                alt="A 3D model of an astronaut"
                shadow-intensity="1"
                camera-controls
                auto-rotate
                min-camera-orbit={`${ minXAngle !== '' ? minXAngle + 'deg' : 'auto' } ${ minYAngle !== '' ? minYAngle + 'deg' : 'auto' } ${ minZoomDis !== '' ? minZoomDis + 'm' : 'auto' }`}
                max-camera-orbit={`${ maxXAngle !== '' ? maxXAngle + 'deg' : 'auto' } ${ maxYAngle !== '' ? maxYAngle + 'deg' : 'auto' } ${ maxZoomDis !== '' ? maxZoomDis + 'm' : 'auto' }`}
            >
            </model-viewer>
        </div>
    )
}

export default Model;